import { AngularFireDatabase } from 'angularfire2/database';
import { Injectable } from '@angular/core';
import answer from '../src/app/answer';

const randrom = (data) => Math.floor(Math.random() * data.length) + 0;

@Injectable({
  providedIn: 'root',
})

export class ChatService {
  currentChat: number = 0;
  name: string = 'Donald John Trump';
  timerID;

  constructor(public db: AngularFireDatabase) {}

  getChatList() {
    return this.db.list('/chats').valueChanges();
  }

  activeChat() {
    return this.db.list(`/chats/${this.currentChat}`).valueChanges();
  }

  selectedChat(name: string, index: number) {
    this.currentChat = index;
    this.name = name;
  }

  getMessages() {
    return this.db.list(`/dialogues/${this.name}/dialogs`).valueChanges();
  }

  addMessage(text: string, isBot: boolean, done) {
    this.db.list(`/dialogues/${this.name}/dialogs`).push({message: text, bot: isBot}).then(done);
    this.db.object(`/chats/${this.currentChat}`).update({ lastMessage: text });
  }


  botAnswer(done) {
    clearTimeout(this.timerID);
    this.timerID = setTimeout(() => {
      const randomAnswer = answer[randrom(answer)];
      this.addMessage(`Ответ: ${randomAnswer}`, true, done);
    }, 1500);

  }





}