import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { ChatService } from '../../services';


@Component({
  selector: 'app-chat-nav',
  templateUrl: './chat-nav.component.html',
  styleUrls: ['./chat-nav.component.scss']
})
export class ChatNavComponent implements OnInit {
  @Output() showMessage = new EventEmitter<any>();
  chats;

  constructor(private service: ChatService) {

  }

  ngOnInit() {
    this.chats = this.service.getChatList();

  }

  showChat(name: string, i: number) {
    this.service.selectedChat(name, i);
    this.showMessage.emit();
  }


}
